<template>
  <section id="main-product">
    <div id="header-container">
      <div>
        <button id="add-button"><i class="fas fa-filter"></i></button>
      </div>
      <div id="form-product-container">
        <button id="add-button" v-on:click="showAddForm"><i class="fas fa-plus"></i></button>
        <AddProductForm v-if="isShowed" v-on:closeForm="closeForm" />
      </div>
    </div>
    <div id="table-product-container">
      <div class="product-header">
        <i class="fas fa-list"></i> Order list
      </div>
      <div class="product-table">
        <table>
          <thead>
            <tr id="rowhead">
              <th class="id-cols">ID</th>
              <th>Name</th>
              <th>Price</th>
              <th>Stock</th>
              <th>Purchase Total</th>
              <th class="action-cols">Action</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="product in getProducts" v-bind:key="product.id">
              <td class="id-cols">{{ product.id }}</td>
              <td>{{ product.name }}</td>
              <td>Rp. {{ product.price }}</td>
              <td>{{ product.stock }} Units</td>
              <td>Rp. {{ product.total }}</td>
              <td class="action-cols">
                <button id="trash-btn" v-on:click="deleteEntry(product.id)"><i class="fas fa-trash-alt"></i></button>
                <button id="edit-btn" v-on:click="updateEntry(product)"><i class="fas fa-edit"></i></button>
              </td>
            </tr>
            <EditProductForm v-if="editMode" v-on:closeForm="closeForm" />
          </tbody>
        </table>
      </div>
    </div>
  </section>
</template>

<script>
import AddProductForm from './2TierComponents/addProductForm.vue'
import EditProductForm from './2TierComponents/editProductForm.vue'

export default {
  data () {
    return {
      isShowed: false,
      editMode: false
    }
  },
  components: { AddProductForm, EditProductForm },
  computed: {
    getProducts () {
      return this.$store.state.products
    }
  },
  methods: {
    showAddForm () {
      this.$store.dispatch('getCategories')
        .then((result) => {
          this.$store.commit('setCategories', { data: result.data.data })
          this.isShowed = true
        })
        .catch((err) => console.log(err))
    },
    fetchAll () {
      this.$store.dispatch('fetchProducts')
    },
    deleteEntry (id) {
      this.$store.dispatch('deleteAsync', id)
        .then((result) => this.fetchAll())
        .catch((err) => console.log(err))
    },
    updateEntry (product) {
      this.editMode = true
      this.$store.dispatch('getCategories')
        .then((result) => {
          this.$store.commit('editAbleData', { product, categories: result.data.data })
        })
        .catch((err) => console.log(err))
    },
    closeForm (params) {
      this.isShowed = params
      this.editMode = params
      this.fetchAll()
    }
  },
  created () {
    this.fetchAll()
  }
}
</script>

<style scoped>

section#main-product {
  margin-left: 12rem;
  margin-top: 1.5rem;
  max-height: 35rem;
  width: 50rem;
}

div#header-container {
  display: flex;
}

div#table-product-container {
  border: 0.1rem solid #d4d4d4;
  animation: fade-in 1s;
}

div#form-product-container {
  position: relative;
}

.product-header, .product-table {
  padding: 1rem;
}

.product-header{
  background-color: #f7f7f7;
}

table {
  width: 100%;
}

tr#rowhead {
    border-bottom: 0.05rem solid #d4d4d4;
    margin-bottom: 0.5rem;
}

.id-cols{
  width: 2rem;
}

td {
  text-align: right;
}
th, td {
  width: 8rem;
  text-align: center;
}

.action-cols{
  width: 12rem;
  display: flex;
  justify-content: center;
}

tr {
  display: flex;
  padding-bottom: 0.2rem;
}

#add-button, #filter-button {
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  margin: 0.3rem 0;
  cursor: pointer;
  background-color: #189a18;
  color: white;
  border-style: none;
}

#add-button{
  margin-right: 0.5rem
}

button#add-button:hover, button#filter-button:hover {
  background-color: #117111;
}

button#trash-btn, button#edit-btn {
  margin: 0 1rem;
  border-style: none;
  background-color: transparent;
  cursor: pointer;
}

button#trash-btn:hover, button#edit-btn:hover {
  animation: shaking .5s infinite;
}

button#trash-btn:hover{
  color: red
}

button#edit-btn:hover{
  color: blue
}

#filter-button{
  margin-left: 0.5rem
}

i.fas.fa-plus, i.fas.fa-filter {
  font-size: 15pt;
}

@keyframes fade-in {
  0%{ opacity: 0; transform: scale(0) }
  100%{ opacity: 1; transform: scale(1) }
}

@keyframes shaking {
  0%{ transform: rotate(-3deg) scale(1) }
  50%{ transform: rotate(0deg) scale(1.3) }
  100%{ transform: rotate(3deg) scale(1) }
}

</style>
